import {Component, OnInit} from '@angular/core';
import {FormControl, FormGroup} from '@angular/forms';
import {MaillerService} from '../mailler.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-contact-me-page',
  templateUrl: './contact-me-page.component.html',
  styleUrls: ['./contact-me-page.component.css']
})
export class ContactMePageComponent {

  loading = false;
  modal = false;

  constructor(public maillerService: MaillerService,
              public router: Router) {
  }

  contactMeForm = new FormGroup({
    name: new FormControl(),
    email: new FormControl(),
    message: new FormControl()
  });

  goToHomePage(): void {
    this.router.navigate(['/home']);
  }
  onSubmit(): void {
    this.loading = true;
    const user = {
      name: this.contactMeForm.value.name,
      email: this.contactMeForm.value.email,
      message: this.contactMeForm.value.message
    };

    this.maillerService.sendEmail('https://nodemailer-api-yash.herokuapp.com/sendmail', user).subscribe(
      data => {
        const res = data;
        console.log('worked!');
        this.loading = false;
        this.modal = true;
      },
      err => {
        console.log(err);
        this.loading = false;
      }
    );


    this.maillerService.sendConfEmail('https://nodemailer-api-yash.herokuapp.com/sendConfirmMail', user).subscribe(
      data => {
        const res = data;
        console.log('worked!');
        this.loading = false;
      },
      err => {
        console.log(err);
        this.loading = false;
      }
    );
  }
}
